//Ëé∑ÂèñÁΩëÈ°µ‰∏≠ÁöÑ‰∏ãÈù¢ÂÜôÁöÑxÔºåÁÑ∂ÂêéËΩ¨Êç¢‰∏∫ÂØπË±°
const x = localStorage.getItem("x");
const xObject = JSON.parse(x);
//Â£∞Êòé‰∏Ä‰∏™hashÊï∞ÁªÑÊù•Ë¥ÆÂ≠òÊ∑ªÂä†ÂæóÁΩëÈ°µÂæólogoÂíåurl
const hashmap = xObject || [
  {
    logo: "A",
    url: "https://www.acfun.cn",
  },
  {
    logo: "B",
    url: "https://www.bilibili.com",
  },
  {
    logo: "X",
    url: "https://xiedaimala.com",
  },
];

//Â™í‰ΩìÊü•ËØ¢,Âõ†‰∏∫Âú®ÊâãÊú∫Á´ØÂíåPCÁ´ØÊêúÁ¥¢ÁöÑactionÁöÑÂú∞ÂùÄÊòØ‰∏çÂêåÁöÑÔºåÊâÄ‰ª•ÈÄöËøá‰∏Ä‰∏™Âà§Êñ≠Êù•‰øÆÊîπÂú∞ÂùÄ
if ($(".searchForm").width() > 500) {
  $(".searchForm").attr("action", "https://www.baidu.com/s");
}
//ÁÆÄÂåñÊòæÁ§∫ÁöÑurl
const simplyUrl = (url) => {
  return url
    .replace("http://", "")
    .replace("https://", "")
    .replace("www.", "")
    .replace(/\/.*/, "");
};
//Ëé∑Âèñul
const $siteList = $(".siteList");
//Ëé∑ÂèñÊúÄÂêé‰∏Ä‰∏™li
const $lastLi = $(".lastLi");
//ÈÅçÂéÜhashÊï∞ÁªÑÂæóÂáΩÊï∞ÔºåÂπ∂‰∏îÊääÊï∞ÁªÑÈáåÈù¢ÂÇ®Â≠òÂæóÁΩëÈ°µÊîæÂà∞ÊúÄÂêé‰∏Ä‰∏™Ê∑ªÂä†ÊåâÈíÆÂæóÂâçÈù¢
const render = () => {
  //Ê∑ªÂä†‰πãÂâçÂÖàÊää‰πãÂâçÁöÑÁΩëÈ°µÂà†Èô§
  $siteList.find("li:not(.lastLi)").remove();
  hashmap.forEach((node, index) => {
    const $li = $(`<li>
                        <div class="site">
                            <div class="logo">${node.logo[0]}</div>
                            <div class="link">${simplyUrl(node.url)}</div>
                            <svg class="icon close">
                              <use xlink:href="#icon-close"></use>
                            </svg>
                        </div>
                </li>`).insertBefore($lastLi);
    //ËÆæÁΩÆÁîüÊàêÁöÑËÉåÊôØÈ¢úËâ≤
    let color = "#" + Math.floor(Math.random() * (2 << 23)).toString(16);
    $li.css("background", color);
    $li.on("click", () => {
      window.open(node.url);
    });
    $li.on("click", ".close", (e) => {
      e.stopPropagation(); //ÈòªÊ≠¢‰∫ã‰ª∂ÂÜíÊ≥°
      hashmap.splice(index, 1);
      render();
    });
  });
};
//ÁΩëÈ°µÁöÑÊúÄÂºÄÂßãÈÅçÂéÜhashÊù•Ê∏≤ÊüìÁΩëÈ°µ
render();
//ÁÇπÂáªÊ∑ªÂä†ÊåâÈíÆ‰πãÂêéÂêëhashÊï∞ÁªÑÈáåÈù¢Ê∑ªÂä†Ë¶ÅÊ∑ªÂä†ÁΩëÈ°µÁöÑ‰ø°ÊÅØÔºåÁÑ∂ÂêéÊ∏≤ÊüìhashÊï∞ÁªÑ
$(".addButton").on("click", () => {
  let url = window.prompt("ËØ∑ËæìÂÖ•‰Ω†Ë¶ÅÊ∑ªÂä†ÂæóÁΩëÂùÄÔºö");
  if(url === ""){
    return;
  }else if(url.indexOf("http") !== 0) {
    url = "https://" + url;
  }
  hashmap.push({ logo: simplyUrl(url)[0], url: url });
  render();
});

window.onbeforeunload = () => {
  const string = JSON.stringify(hashmap);
  localStorage.setItem("x", string);
};
